<template>
  <div class="page has-navbar" v-nav="{title: '', showBackButton: true}">
    <div class="page-content padding-top">

      <div class="item item-icon-right thin-border" @click="showAlert()">
        Alert <span class="item-note"></span>
        <i class="icon ion-ios-arrow-right"></i>
      </div>
      <div class="item item-icon-right thin-border" @click="showConfirm()">
        Confirm <span class="item-note"></span>
        <i class="icon ion-ios-arrow-right"></i>
      </div>
      <div class="item item-icon-right thin-border" @click="showAlertIOS()">
        iOS Alert <span class="item-note">iOS </span>
        <i class="icon ion-ios-arrow-right"></i>
      </div>
      <div class="item item-icon-right thin-border" @click="showConfirmIOS()">
        iOS Confirm <span class="item-note">iOS </span>
        <i class="icon ion-ios-arrow-right"></i>
      </div>
      <div class="item item-icon-right thin-border" @click="multiMode()">
        Multi Mode <span class="item-note"></span>
        <i class="icon ion-ios-arrow-right"></i>
      </div>

      <div class="item item-icon-right thin-border" @click="showCustomPopup()">
        Custom Popup
        <i class="icon ion-ios-arrow-right"></i>
      </div>
      <div class="item item-icon-right thin-border" @click="showCustomPopup2()">
        Custom Popup 2
        <i class="icon ion-ios-arrow-right"></i>
      </div>

      <div class="item item-icon-right thin-border" @click="showAlert('scale')">
        Scale Effect
        <i class="icon ion-ios-arrow-right"></i>
      </div>
      <div class="item item-icon-right thin-border" @click="showAlert('slide')">
        Slide Effect
        <i class="icon ion-ios-arrow-right"></i>
      </div>

      <div style="height: 50px;"></div>

    </div>
  </div>
</template>
<script>
  export default {

    data(){
      return {
      }
    },

    methods: {
      showAlert(effect) {
        $dialog.alert({
          content: 'Hello!',
          okTheme: 'positive',
          effect: effect || 'default'
        }).then(() => {
          console.log('alert hide.')
        })
      },

      showConfirm() {
        $dialog.confirm({
          content: '""?',
          okText: ''
        }).then((res) => {
          console.log('confirm result: ', res)
        })
      },

      showAlertIOS() {
        $dialog.alert({
          theme: 'ios',
          title: '',
          content: 'Safari',
          okText: ''
        }).then(() => {
          console.log('alert hide.')
        })
      },

      showConfirmIOS() {
        $dialog.confirm({
          theme: 'ios',
          title: '""?',
          cancelText: '',
          okText: ''
        }).then((res) => {
          console.log('confirm result: ', res)
        })
      },

      showCustomPopup() {
        let options = {
          effect: 'scale',
          title: '',
          buttons: [
            {text: ''},
            {text: ''},
          ]
        }

        let popup = $popup.fromTemplate('<p style="margin-bottom: 0; text-align: center;"></p>', options)

        popup.show().then((buttonIndex) => {
          console.log(buttonIndex)
        })
      },

      showCustomPopup2() {
        let options = {
          effect: 'scale',
          title: '',
          buttons: [
            {text: '', theme: 'assertive'}
          ],
          showClose: true
        }

        let template = `
          <p style="margin-bottom: 10px; text-align: center; font-size: 16px;"></p>
          <p style="margin-bottom: 0; text-align: center;"></p>
        `

        let popup = $popup.fromTemplate(template, options)

        popup.show().then((buttonIndex) => {
          console.log(buttonIndex)
        })

        /* fromTemplateUrl sample */

        // let path_to_template = ''
        // $popup
        //   .fromTemplateUrl(path_to_template, options)
        //   .then((popup) => {
        //     popup.show()
        //   })

      },

      multiMode() {
        let options = {
          theme: 'ios',
          title: '',
          okText: ''
        }

        for (let i = 0; i < 3; i++) {
          setTimeout(() => {
            options.content = `${i+1}`
            $dialog.alert(options)
          }, 500 * i)
        }
      }
    }
  }
</script>



